.background {
  position: absolute;
  background-color: black;
  width: 100%;
  height: 100%;
}

.registerBox {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  padding: 40px;
  transform: translate(-50%, -50%);
  background: darkgrey;
  box-shadow: 0 15px 25px;
  border-radius: 5px;

  min-height: 50vh;
}

.registerBox h2 {
  margin: 0 0 30px;
  padding: 0;
  color: blue;
  text-align: center;
}

.registerBox .registerData input {
  position: relative;
  width: 100%;

  padding: 10px 0;
  font-size: 16px;
  color: black;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid black;
  outline: none;
  background: transparent;
}

.registerBox .registerData label {
  position: relative;
  left: 0;
  top: -60px;
  padding: 10px 0;
  color: black;
  font-size: 16px;
  pointer-events: none;
  transition: 0.5s;
}

.registerBox .registerData input:focus ~ label,
.registerBox .registerData input:focus ~ label {
  top: -85px;
  left: 0;
  color: blue;
  font-size: 12px;
}

.button {
  position: relative;
  left: 0%;
  padding: 5px 10px;
  color: black;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: none;
  transition: 0.5s;
  letter-spacing: 4px;
  border: 1px solid blue;
  margin: auto;
  cursor: pointer;
  width: 100px;
  margin-bottom: 20px;
}

.button:hover {
  background: blue;
  color: black;
  border-radius: 3px;
  box-shadow: 0 0 5px white, 0 0 25px white, 0 0 50px white;
}

.button {
  flex-direction: row;
}
